<template>
    <div class="container">
      <h1>库位库区商品(列表)</h1> 
      <div class="tableContainer">
        <el-table
      :data="tableData"
      style="width: 100%">
      <el-table-column
        prop="id"
        label="ID"
        width="80">
      </el-table-column> 
      <el-table-column
        prop="goods_id"
        label="商品ID"
        width="180">
      </el-table-column>
      <el-table-column
        prop="sku"
        label="SKU">
      </el-table-column>
      <el-table-column
        prop="number"
        label="商品数量">
      </el-table-column>
       
      <el-table-column
            label="操作">
            <template slot-scope="scope"> 
              <el-button @click="handleDetail(scope.row)" type="text" size="small">查看流水</el-button> 
            </template>
        </el-table-column>


      </el-table>
        
      </div>
    </div>
</template>

<script>
    
  export default {
    name: "warehouse",
    data() {
        return {
          tableData: [/*{
            "id": "1",
            "code": "AAA",
            "name": "A仓库",
            "country": "中国",
            "mobile": "12312323",
            "address": "东莞",
            "type": "自建仓"
          },
          {
            "id": "1",
            "code": "AAA",
            "name": "A仓库",
            "country": "中国",
            "mobile": "12312323",
            "address": "东莞",
            "type": "自建仓"
          },*/ 
          ],
          totalSize: 0,
          currentPage: 1,
          pageSize: 20
        }
    },
    methods: {
      filterDegree() {

      },
      filterStatus() {

      },
      handleSizeChange(val) {
        this.pageSize = val;
        this.initTable();
      },
      handleCurrentChange(val) {
        this.currentPage = val;
        this.initTable();
      },
      
      handleDetail(row) {
        this.$router.push({ path: '/sub-inventory-sku/log',query: row})
      },
      
      initTable() {
        console.log(2223);
        let initList = [];
        let url = "http://api.orkowms.me/sub-inventory-sku?page="+this.currentPage+"&per-page="+this.pageSize;
        let row = this.$route.query;   
        if(row.id){
           url = url + "&sub_inventory_id="+row.id;
        }
         
        let that = this;
        let config = {
          headers : { 
            'Content-Type':'application/json;charset=UTF-8'
          },
        }; 
        this.$axios.get(url,config).then((res) => {  
          if(res.data.data){ 
            let tasksList = res.data.data ;
            that.totalSize = parseInt(res.data._meta.totalCount);
            tasksList.forEach((task) => {
              let tempTask = {};
              tempTask.id = task.id;
              tempTask.goods_id = task.goods_id;
              tempTask.sku = task.sku;
              tempTask.sub_inventory_id = task.sub_inventory_id;
              tempTask.number = task.number;
              
              initList.push(tempTask);
            })
          }
          that.tableData = initList; 
          console.log(res)
        }).catch((err) => {
          console.log(err)
        })
      }
    },
    created() {
      
      this.initTable();
    }
  }
</script>

<style lang="stylus">
  .container
    padding 30px
    position relative
    .addItem
      margin 20px 0
    .tableContainer
      position absolute
      left 30px
      right: 30px
      top: 148px
      bottom: 0
      display flex
      flex-direction column
      .table
        flex 1
        .demo-table-expand
          font-size: 0
          label
            width: 90px
            color: #99a9bf
          .el-form-item
            margin-right: 0
            margin-bottom: 0
            width: 33.333%
        td.markColor
          color #00a0e9
        a
          color #03B8CC
      .el-pagination
        text-align right
        margin 8px 0
</style>



